"use client"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { useState } from "react"

interface Product {
  id: string
  code: string
  name: string
  entries: number
  exits: number
  stock: number
}

export default function InventoryPage() {
  const [products, setProducts] = useState<Product[]>([
    { id: "1", code: "1", name: "Regla de 30 cm", entries: 10, exits: 2, stock: 8 },
    { id: "2", code: "2", name: "Lápiz HB", entries: 50, exits: 15, stock: 35 },
    { id: "3", code: "3", name: "Goma de borrar azul", entries: 30, exits: 10, stock: 20 },
  ])

  const [newProduct, setNewProduct] = useState<Product>({
    id: "",
    code: "",
    name: "",
    entries: 0,
    exits: 0,
    stock: 0
  })

  const [editingProduct, setEditingProduct] = useState<Product | null>(null)

  const addProduct = () => {
    if (newProduct.code && newProduct.name) {
      const stock = newProduct.entries - newProduct.exits
      const productToAdd = {
        ...newProduct,
        id: Date.now().toString(),
        stock
      }
      setProducts([...products, productToAdd])
      setNewProduct({ id: "", code: "", name: "", entries: 0, exits: 0, stock: 0 })
    }
  }

  const updateProduct = (product: Product) => {
    const updatedProducts = products.map(p => 
      p.id === product.id 
        ? { ...product, stock: product.entries - product.exits }
        : p
    )
    setProducts(updatedProducts)
    setEditingProduct(null)
  }

  const deleteProduct = (id: string) => {
    setProducts(products.filter(p => p.id !== id))
  }

  const startEditing = (product: Product) => {
    setEditingProduct(product)
  }

  return (
    <div className="container mx-auto p-4 space-y-6 max-w-[1200px]">
      <h1 className="text-4xl font-bold text-center bg-[#1e3a8a] text-white py-4 rounded-none shadow-md">
        INVENTARIO EN EXCEL
      </h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card className="shadow-lg border-t-4 border-t-[#1e3a8a]">
          <CardHeader className="bg-gray-50 border-b">
            <CardTitle className="text-2xl font-bold text-[#1e3a8a]">PRODUCTOS</CardTitle>
          </CardHeader>
          <CardContent className="p-0">
            <div className="space-y-4">
              <div className="grid grid-cols-5 gap-2 p-4 bg-white">
                <Input 
                  placeholder="Código"
                  value={newProduct.code}
                  onChange={(e) => setNewProduct({...newProduct, code: e.target.value})}
                />
                <Input 
                  placeholder="Nombre"
                  value={newProduct.name}
                  onChange={(e) => setNewProduct({...newProduct, name: e.target.value})}
                />
                <Input 
                  type="number"
                  placeholder="Entradas"
                  value={newProduct.entries || ""}
                  onChange={(e) => setNewProduct({...newProduct, entries: parseInt(e.target.value) || 0})}
                />
                <Input 
                  type="number"
                  placeholder="Salidas"
                  value={newProduct.exits || ""}
                  onChange={(e) => setNewProduct({...newProduct, exits: parseInt(e.target.value) || 0})}
                />
                <Button 
                  onClick={addProduct}
                  className="bg-[#1e3a8a] hover:bg-[#1e3a8a]/90"
                >
                  Agregar
                </Button>
              </div>

              <Table className="border">
                <TableHeader>
                  <TableRow className="bg-gray-100">
                    <TableHead className="font-bold border bg-gray-100">CÓDIGO</TableHead>
                    <TableHead className="font-bold border bg-gray-100">NOMBRE</TableHead>
                    <TableHead className="font-bold border bg-gray-100">ENTRADAS</TableHead>
                    <TableHead className="font-bold border bg-gray-100">SALIDAS</TableHead>
                    <TableHead className="font-bold border bg-gray-100">STOCK</TableHead>
                    <TableHead className="font-bold border bg-gray-100">ACCIONES</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {products.map((product) => (
                    <TableRow key={product.id}>
                      {editingProduct?.id === product.id ? (
                        <>
                          <TableCell className="border">
                            <Input 
                              value={editingProduct.code}
                              onChange={(e) => setEditingProduct({...editingProduct, code: e.target.value})}
                            />
                          </TableCell>
                          <TableCell className="border">
                            <Input 
                              value={editingProduct.name}
                              onChange={(e) => setEditingProduct({...editingProduct, name: e.target.value})}
                            />
                          </TableCell>
                          <TableCell className="border">
                            <Input 
                              type="number"
                              value={editingProduct.entries}
                              onChange={(e) => setEditingProduct({...editingProduct, entries: parseInt(e.target.value) || 0})}
                            />
                          </TableCell>
                          <TableCell className="border">
                            <Input 
                              type="number"
                              value={editingProduct.exits}
                              onChange={(e) => setEditingProduct({...editingProduct, exits: parseInt(e.target.value) || 0})}
                            />
                          </TableCell>
                          <TableCell className="border font-medium">
                            {editingProduct.entries - editingProduct.exits}
                          </TableCell>
                          <TableCell className="border">
                            <div className="flex gap-2">
                              <Button 
                                onClick={() => updateProduct(editingProduct)}
                                className="bg-green-600 hover:bg-green-700"
                              >
                                Guardar
                              </Button>
                              <Button 
                                onClick={() => setEditingProduct(null)}
                                variant="destructive"
                              >
                                Cancelar
                              </Button>
                            </div>
                          </TableCell>
                        </>
                      ) : (
                        <>
                          <TableCell className="border">{product.code}</TableCell>
                          <TableCell className="border">{product.name}</TableCell>
                          <TableCell className="border text-green-600 font-medium">{product.entries}</TableCell>
                          <TableCell className="border text-red-600 font-medium">{product.exits}</TableCell>
                          <TableCell className="border font-medium">{product.stock}</TableCell>
                          <TableCell className="border">
                            <div className="flex gap-2">
                              <Button 
                                onClick={() => startEditing(product)}
                                className="bg-blue-600 hover:bg-blue-700"
                              >
                                Editar
                              </Button>
                              <Button 
                                onClick={() => deleteProduct(product.id)}
                                variant="destructive"
                              >
                                Eliminar
                              </Button>
                            </div>
                          </TableCell>
                        </>
                      )}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>

        <Card className="shadow-lg border-t-4 border-t-[#1e3a8a]">
          <CardHeader className="bg-gray-50 border-b">
            <CardTitle className="text-2xl font-bold text-[#1e3a8a]">ENTRADAS | SALIDAS</CardTitle>
          </CardHeader>
          <CardContent className="p-0">
            <Table className="border">
              <TableHeader>
                <TableRow className="bg-gray-100">
                  <TableHead className="font-bold border bg-gray-100">CÓDIGO</TableHead>
                  <TableHead className="font-bold border bg-gray-100">NOMBRE</TableHead>
                  <TableHead className="font-bold border bg-gray-100">CANTIDAD</TableHead>
                  <TableHead className="font-bold border bg-gray-100">TIPO</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {products.flatMap((product) => {
                  const rows = [];
                  if (product.entries > 0) {
                    rows.push(
                      <TableRow key={`${product.id}-entry`}>
                        <TableCell className="border">{product.code}</TableCell>
                        <TableCell className="border">{product.name}</TableCell>
                        <TableCell className="border text-green-600 font-medium">{product.entries}</TableCell>
                        <TableCell className="border bg-green-50">Entrada</TableCell>
                      </TableRow>
                    );
                  }
                  if (product.exits > 0) {
                    rows.push(
                      <TableRow key={`${product.id}-exit`}>
                        <TableCell className="border">{product.code}</TableCell>
                        <TableCell className="border">{product.name}</TableCell>
                        <TableCell className="border text-red-600 font-medium">{product.exits}</TableCell>
                        <TableCell className="border bg-red-50">Salida</TableCell>
                      </TableRow>
                    );
                  }
                  return rows;
                })}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
